:root {
  --tree-node-fill: #8bc34a;
  --tree-node-stroke: #5d4037;
  --tree-male-color: #90caf9;
  --tree-female-color: #f48fb1;
  --tree-link-color: #795548;
  --tree-text-color: #212121;
  --tree-text-secondary: #757575;
  --tree-boy-child-color: #8bc34a;
  --tree-girl-child-color: #f8bbd0;
}

.family-tree-svg {
  width: 100%;
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

.node-box {
  stroke: var(--tree-node-stroke);
  stroke-width: 2px;
  fill: var(--tree-node-fill);
  rx: 5px;
  ry: 5px;
}

.male-node {
  fill: var(--tree-male-color);
}

.female-node {
  fill: var(--tree-female-color);
}

.boy-child-node {
  fill: var(--tree-boy-child-color);
}

.girl-child-node {
  fill: var(--tree-girl-child-color);
}

.male-silhouette path, .female-silhouette path, .couple-silhouette path {
  fill: #444;
  stroke: #212121;
  stroke-width: .5px;
}

.link {
  fill: none;
  stroke: var(--tree-link-color);
  stroke-width: 4px;
  stroke-linecap: round;
}

.link-marriage {
  fill: none;
  stroke: var(--tree-link-color);
  stroke-width: 3px;
  stroke-dasharray: 5 5;
}

.text-content {
  pointer-events: none;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.name {
  fill: var(--tree-text-color);
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 14px;
  font-weight: bold;
}

.spouse-name {
  fill: var(--tree-text-color);
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 14px;
}

.lifespan, .spouse-lifespan {
  fill: var(--tree-text-secondary);
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 12px;
}

.native-place {
  fill: var(--tree-text-secondary);
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 12px;
  font-style: italic;
}

.birth-order {
  fill: var(--tree-text-secondary);
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 11px;
}

.spouse-divider {
  stroke: var(--tree-text-secondary);
  stroke-width: 1px;
  stroke-dasharray: 2 2;
}

.arrow-head {
  fill: var(--tree-link-color);
}

.node:hover .node-box {
  stroke-width: 3px;
  cursor: pointer;
}

.node text, .node tspan {
  text-anchor: middle;
}

@media (width <= 768px) {
  .name, .spouse-name {
    font-size: 12px;
  }

  .lifespan, .spouse-lifespan, .native-place {
    font-size: 10px;
  }
}

